.friendMessage {
  margin-top: 35px;
}

.friendImageAndText {
  display: flex;
  max-height: 60px;
}

.friendImageAndText img {
  width: 48px;
  height: 48px;
}

.friendText {
  order: -1;
  padding-right: 20px;
  position: relative;
  max-width: 575px;
  height: 100%;
  width: 100%;
  text-align: right;
}

.friendText:after {
  content: '';
  position: absolute;
  background-image: url('../text_blue.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  top: -25px;
  right: -25px;
  z-index: -1;
}

.friendName {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
}

.friendMessageText {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fff;
  height: 77px;
}

.friendTime {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 10px;
  color: #fff;
}
