.message {
  margin-top: 35px;
  margin-bottom: 35px;
}

.imageAndText {
  display: flex;
  max-height: 60px;
}

.imageAndText img {
  width: 48px;
  height: 48px;
}

.text {
  padding-left: 25px;
  position: relative;
  max-width: 575px;
  height: 100%;
  width: 100%;
}

.text:after {
  content: '';
  position: absolute;
  background-image: url('../text.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  top: -25px;
  left: -25px;
  z-index: -1;
}

.name {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #000;
}

.messageText {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #000;
  height: 77px;
}

.time {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 10px;
  color: #000;
}