.sendForm {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
}

.textarea {
  margin-right: 40px;
  padding: 12px 25px;
  border-radius: 20px;
  width: 976px;
  height: 40px !important;
  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
  background: #f5f7fb;
  height: 100px;        /* Фиксированная высота */
  overflow: hidden;     /* Скрыть содержимое, выходящее за границы */
  display: block; 
}

.textarea::placeholder {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
  opacity: 0.9;
}

.button {
  border-radius: 20px;
  width: 120px;
  height: 40px;
  background: #06c;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
}

.button:hover {
  background: orange;
}
.button:active {
  background-color: violet;
}
